import { Icoins } from './types/vendingMachineCoinManager';
import {
  productQuantity,
  productName,
  productPrice,
  chargeMoney,
  errorMessage,
  routes,
} from './types/constants';

export const PRODUCT_NAME: productName = {
  MAX_LENGTH: 10,
  MIN_LENGTH: 1,
};

export const PRODUCT_PRICE: productPrice = {
  MAX_PRICE: 10000,
  MIN_PRICE: 100,
  UNIT: 10,
};

export const PRODUCT_QUANTITY: productQuantity = {
  MAX_QUANTITY: 20,
  MIN_QUANTITY: 1,
};

export const CHARGE_MONEY: chargeMoney = {
  MAX_TOTAL_CHARGE_MONEY: 100000,
  UNIT: 10,
};

export const ERROR_MESSAGE: errorMessage = {
  WRONG_LENGTH_PRODUCT_NAME: `  .  ${PRODUCT_NAME.MIN_LENGTH}  ${PRODUCT_NAME.MAX_LENGTH}  .`,
  DUPLICATED_PRODUCT_NAME:
    '  .     .',
  WRONG_RANGE_PRODUCT_PRICE: `   .   ${PRODUCT_PRICE.MIN_PRICE}  ${PRODUCT_PRICE.MAX_PRICE}  .`,
  WRONG_UNIT_PRODUCT_PRICE: `   .   ${PRODUCT_PRICE.UNIT}  .`,
  WRONG_PRODUCT_QUANTITY: `   .    ${PRODUCT_QUANTITY.MIN_QUANTITY}   ${PRODUCT_QUANTITY.MAX_QUANTITY}  .`,
  WRONG_UNIT_CHARGE_MONEY: `  .  ${CHARGE_MONEY.UNIT}  .`,
  EMPTY_PRODUCT_NAME:
    '  .   .',
  OVERFLOW_CHARGE_MONEY(chargeMoney) {
    return `   ${CHARGE_MONEY.MAX_TOTAL_CHARGE_MONEY} .<br/>   ${chargeMoney} .   ${CHARGE_MONEY.MAX_TOTAL_CHARGE_MONEY}   .`;
  },
};

